import { ApiGeneralService } from 'src/app/services/api-general.service';
import { ApiCRMService } from 'src/app/services/api-crm.service';
import { CallNumber } from '@ionic-native/call-number/ngx';
import { Component, OnInit } from '@angular/core';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { environment } from 'src/environments/environment.prod';
import { DomSanitizer } from '@angular/platform-browser';
import { ToastService } from 'src/app/services/toast.service';

@Component({
  selector: 'app-moksh-plus',
  templateUrl: './moksh-plus.page.html',
  styleUrls: ['./moksh-plus.page.scss'],
})
export class MokshPlusPage implements OnInit {
  isVideoOpen : boolean ;
  iframe : any;

  arrServices = [];
  webAssetsPath = "";
  arrVideoMBBS = [];
  currentSelected = 0;
  slideVideoOpts = {
    slidesPerView: 1.5   
  }
  arrCounsellor : any = {"empMob2":"", "empMob1":""};
  
  constructor(private headerTitle: HeaderTitleService,         
    private call : CallNumber,    
    private apiCRMService : ApiCRMService,
    private apiGeneralService : ApiGeneralService,
    private sanitizer : DomSanitizer,
    private toastr : ToastService
    ) { 
    this.headerTitle.isBack = false;
    this.headerTitle.title = "MOKSH PLUS";
  }

  ngOnInit() {    
    setTimeout(() => {
      this.getCounsellor();
    }, 500);
    this.getVideos();
  }
  onClickCall(){    
    console.log(this.apiCRMService.arrCounsellor["empMob1"]);
    this.call.callNumber(this.apiCRMService.arrCounsellor["empMob1"], true)
        .then(res => console.log('Launched dialer!', res))
        .catch(err => console.log('Error launching dialer', err));
  }
  async getCounsellor(){
    this.arrCounsellor = this.apiCRMService.arrCounsellor;    
  }
  
  //#region Video Section start
  async getVideos() {
    let postData = { 'videoHeaderId': 27 }
    await this.apiGeneralService.getVideos(JSON.stringify(postData)).then(resp => {
      this.webAssetsPath = environment.webAssetsPath+ "mobility-app/videothumbnails";
      this.arrVideoMBBS = resp;
    })
      .catch(err => {
        console.log("getVideos: " + err);
      })
  }
  playVideo(videoSrc){
    if(videoSrc == ""){
      this.toastr.toastError("Sorry! Currently this video is not available.");
      return false;
    }
    this.iframe = this.sanitizer.bypassSecurityTrustHtml('<iframe src="'+videoSrc+'" frameborder="0" style="width: 100%; height: 300px;"allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>');    
    this.isVideoOpen = true;
  }
  videoPanelClose(){
    this.iframe = "";
    this.isVideoOpen = false;
  }
  //#endregion
}
